
section {
    overflow: hidden;
    background: url(../images/bg/section.png) no-repeat top center;
    background-size: cover;
    padding-top: 50px;
    padding-bottom: 150px;
    
    position: relative;
    &:after {
        content: '';
        display: block;
        width: 1920px;
        height: 85px;
        background: $white;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%,0);
        
       -webkit-clip-path: polygon(56.5% 3px, 100% 100%, 0 100%, 41.5% 4px, 49% 100%);
        clip-path: polygon(56.5% 3px, 100% 100%, 0 100%, 41.5% 4px, 49% 100%);
    }
    h1 {
        color: $black;
        font-family: 'Montserrat', sans-serif;
        font-size: 42px;
        @media (max-width:815px) {
            font-size: 30px;   
        }
        i {
            font-style: normal;
            color: $orange;
        }
        span {
            font-size: 32px;
            display: block;
            @media (max-width:815px) {
                font-size: 20px;   
            }
        }
    }
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width:700px) {
            flex-wrap: wrap;   
        }
    }
}
.form {
    &_block {
        max-width: 460px;
        background: rgba(243, 98, 35, 0.9);
        transform: skewX(-15deg);
        padding: 50px 80px;
        color: $white;
        text-align: center;
        @media (max-width:950px) {
            padding: 30px 40px;
        }
        @media (max-width:800px) {
            transform: skewX(-10deg);
        }
        @media (max-width:700px) {
            max-width: 100%;
            margin-top: 25px;
        }
        @media (max-width:350px) {
            transform: skewX(-5deg);
        }
        & > * {
            transform: skewX(15deg);
            @media (max-width:800px) {
                transform: skewX(10deg);
            }
            @media (max-width:350px) {
                transform: skewX(5deg);
            }
        }
    }
    &_head {
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        text-align: center;
        font-size: 22px;
        margin-bottom: 15px;
    }
}

.list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &_item {
        width: 30%;
        @media (max-width:780px) {
            width: 48%;
        }
        @media (max-width:600px) {
            width: 98%;
            margin-left: 1%;
        }
    }
    &_img {
        border: 4px solid $orange;
    }
    img {
        display: block;
        width: 100%;
    }
    &_top {
        font-family: 'Montserrat', sans-serif;
        font-size: 18px;
        padding: 15px 0 24px 0;
        text-align: center;
        font-weight: bold;
    }
    &_li {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 20px;
        i {
            width: 16px;
            height: 12px;
            min-width: 16px;
            background: url(../images/ch.png) no-repeat 0 0;
            margin-right: 11px;
                margin-top: 6px;
        }
    }
}

.section {
    padding: 15px 0;
    &_head {
        font-size: 32px;
        color: $black;
        text-align: center;
        position: relative;
        font-family: 'Montserrat', sans-serif;
        font-weight: bold;
        margin-bottom: 45px;
        overflow: hidden;
        @media (max-width:450px) {
            font-size: 22px;
        }
        &.lit {
            max-width: 810px;
            margin-left: auto;
            margin-right: auto;
        }
        span {
            display: inline-block;
            padding: 0 45px;
            
            position: relative;
            z-index: 2;
            @media (max-width:450px) {
                padding: 0 25px;
            }
            &:before,
            &:after {
                content: '';
                display: block;
                
                height: 4px;
                background: $black;
                position: absolute;
                top: 50%;
                margin-top: -2px;
                left: 100%;
                right: -9999px;
            }
            &:before {
                left: -99999px;
                right: 100%;
            }
        }
    }
    &_2 {
        background: url(../images/bg/2.png) no-repeat top center;
        background-size: cover; 
        padding: 0;
        .form {
            &_block {
                width: 100%;
                max-width: 100%;
                color: #fff;
            }
        }
        .flex {
            @media (max-width:800px) {
                flex-wrap: wrap;
            }
            * {
                @media (max-width:800px) {
                    width: 100%;   
                }
                & + * {
                    margin-left: 10px;
                    white-space: nowrap;
                    min-width: auto;
                    @media (max-width:800px) {
                        margin-left: 0;
                
                    }
                }
            }
        }
        input {
            margin-bottom: 0;
            @media (max-width:800px) {
                margin-bottom: 10px;   
            }
        }
        .section {
            &_head {
                color: $white;
                margin-bottom: 30px;
                span {
                    &:before,
                    &:after {
                        background:$white;
                    }
                }
            }
        }
    }
}

.info {
    align-items: center;
    font-size: 15px;
    line-height: 35px;
    padding-top: 50px;
    padding-bottom: 100px;
    @media (max-width:800px) {
        flex-wrap: wrap;   
        justify-content: center;
    }
    & > * {
        width: 50%;
        @media (max-width:800px) {
            width: 100%;
            margin: 0!important;
                max-width: 495px;
        }
    }
    img {
        display: block;
        width: 100%;
    }
    &_img {
        margin-right: 10px;
        border-radius: 4px;
        border: 3px solid $orange;
        
        max-width: 495px;
    }
    &_text {
        margin-left: 10px;
        & > div {
            display: flex;
            justify-content: flex-start;
            align-items: center;
        }
        i {
            width: 14px;
            height: 14px;
            min-width: 14px;
            background: url(../images/pl.png) no-repeat center;
            margin-right: 14px;
        }
    }
}

.who {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 60px;
    &_item {
        width: 33%;
        padding-top: 20px;
        padding-bottom: 28px;
        position: relative;
        
        @media (max-width:720px) {
               width: 50%;
        }
        @media (max-width:485px) {
               width: 100%;
        }
        @media (min-width:720px) {
            & + div {
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 20px;
                    left: 0px;
                    bottom: 20px;
                    width: 1px;
                    background: #d4d4d4;
                }
            }
            &:nth-child(3) {
                & + div {
                    &:after {
                        display: none;
                    }
                }
                & ~ div {
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 20px;
                        right: 20px;
                        height: 1px;
                        background: #d4d4d4;
                    }
                }
            }
        }
        @media (max-width:720px) and (min-width:485px) {
            & + div {
                
            }
            &:nth-child(even) {
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 20px;
                    left: 0px;
                    bottom: 20px;
                    width: 1px;
                    background: #d4d4d4;
                }
            }
            &:nth-child(2) ~ div {
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 20px;
                        right: 20px;
                        height: 1px;
                        background: #d4d4d4;
                    }
            }
            
        }
        @media (max-width:485px) {
            
            &:nth-child(1) ~ div {
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 20px;
                        right: 20px;
                        height: 1px;
                        background: #d4d4d4;
                    }
            }
            
        }
        
    }
    img {
        display: block;
        margin: 0 auto 27px auto;
    }
}

.rev {
    width: auto;
    font-size: 16px;
    padding-bottom: 35px;
    margin-left: 35px;
    margin-right: 35px;
    
    &_item {
        padding: 37px 50px;
        display: flex;
        background: #eeeeee;
        border-radius: 5px;
        @media (max-width:500px) {
            padding: 25px 15px;
        }
    }
    &_img {
        margin-right: 60px;
        @media (max-width:620px) {
            display: none;
        }
    }
    b {
        display: block;
        margin-bottom: 20px;
    }
}
.owl-nav {
    height: 0;
    [type="button"] {
        span {
            text-indent: -9999px; 
            width: 17px;
            height: 33px;
            position: absolute;
            top: 50%;
            transform: translate(-50%,-50%);
     
            background: url(../images/nav.png) no-repeat;
            &:hover {
                opacity: 0.7;
            }
        }
        &.owl-prev {
            background-position:  top left;
            span {
                left: -34px;   
            }
        }
        &.owl-next {
            background-position:  top right;
            span {
                right: -34px;  
                transform: rotate(-180deg);
                margin-top: -17px;
            }
        }
    }
}
.owl-dots {
    text-align: center;
    margin: 10px 0;
    .owl-dot {
        display: inline-block;
        vertical-align: middle;
        width: 13px;
        height: 13px;
        border-radius: 50%;
        border: 2px solid $orange!important;
        margin: 4px;
        &.active {
            background: $orange!important;
        }
    }
}