    }
}

footer,
header {
    padding-top: 10px;
    padding-bottom: 12px;
    position: relative;
    &:after,
    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        z-index: 1;
        background-size: cover;
        background-repeat: no-repeat;
        
    }
    &:before {
        width: calc( 50% + 15px);
        background-image: url(../images/header/left.jpg);
        left: 0;
        margin-right: -15px;
        background-position: top right
    }
    &:after {
        width: calc( 50% );
        background-image: url(../images/header/right.jpg)  ;
        right: 0;
        z-index: 2;
        background-position: top left;
        -webkit-clip-path: polygon(15px 0%, 100% 0, 100% 100%, 0% 100%);
        clip-path: polygon(15px 0%, 100% 0, 100% 100%, 0% 100%);
    }
    .wrapper {
        position: relative;
        z-index: 3;
        & > div {
            display: flex;
            justify-content: center;
            align-items: center;
            @media (max-width:815px) {
                flex-direction: column;
                .tel {
                    margin-bottom: 10px;
                }
            }
        }
    }
           
        .left {
            width: calc( 50% + 15px);
            justify-content: flex-start!important;
        }
        .right {
            width: calc( 50% - 15px);
            justify-content: space-around!important;
        }
    
    @media (max-width:450px) {
        .left {
            justify-content: flex-start!important;
        }
        .right {
            width: 100%;
        } 
    }
}
footer {
    padding-top: 60px;
    padding-bottom: 60px;
    .tel {
        color: $black;
    }
    &:before {
        background-image: url(../images/header/right.jpg);
    }
    &:after {
        background-image: url(../images/header/left.jpg);
    }
    .right { 
        justify-content: flex-end!important;
    }
    .logo {
        color: #fff;
    }
    .left {
            width: calc( 50% + 15px);
            justify-content: flex-start!important;
        }
        .right {
            width: calc( 50% - 15px);
            justify-content: space-around!important;
        }
    @media (max-width:450px) {
        font-size: 12px;   
    }
}
.logo {
    color: $black;
    font-size: 24px;
    font-weight: 700;
    text-decoration: none;
    span {
        color: $orange;
    }
}
.tel {
    font-weight: 100;
    color: $orange;
    display: flex;
    align-items: center;
    img {
        margin-right: 10px;
    }
}
