@import "variables";
@import "mixins";
html,
body {
    font-family: 'Open Sans', sans-serif;
        overflow-x: hidden;
}
.flex {
    display: flex;
}
.wrapper {
    max-width: 1190px;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
}
.btn {
    color: #fff;
    display: inline-block;
    padding: 8px 0;
    min-width: 175px;
    border: none;
    text-decoration: none;
    font-size: 17px;
    font-weight: bold;
    background: $BGgreen;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    border-radius: 5px;
    @media (max-width:350px) {
        font-size: 16px;
        }
}
input {
    &[type="text"] {
        padding: 15px 10px;
        border-radius: 5px;
        margin-bottom: 10px;
        width: 100%;
        font-size: 14px;
        @include plhl() {
            color: #b1b1b1;
        }
    }
}
button {
//    max-width: 240px;
//    display: block;
    margin: 0 auto;
    &.btn {
        padding-left: 15px;
        padding-right: 15px;
    }
}
h3 {
    font-size: 25px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    margin-bottom: 39px;
}
.center {
    text-align: center;
}
